.show-btn{
  background: #fff;
  padding: 10px 20px;
  font-size: 20px;
  font-weight: 500;
  color: #3498db;
  cursor: pointer;
  box-shadow: 0px 0px 10px rgba(0,0,0,0.1);
}
.contain{
  box-shadow: 0 0 10px rgba(0,0,0,1);
}
.container{
  background: #fff;
  width: 410px;
  padding: 30px 30px;
  margin-top: 1%;
  box-shadow: 0 0 10px rgba(0,0,0,1);
}
.container .text{
  font-size: 35px;
  font-weight: 600;
  text-align: center;
}
.forgot-pass{
  font-size: 15px;
  padding-top: 20px;
}
.signup-link {
  font-size: 15px;
}
.container .logo img{
    width: 100px;
    margin: 0 35%;
}
.container form{
  margin-top: -20px;
}
.container form .data{
  height: 45px;
  width: 100%;
  margin: 40px 0;
}
form .data label{
  font-size: 18px;
}
form .data input{
  height: 100%;
  width: 100%;
  padding-left: 10px;
  font-size: 17px;
  border: 1px solid silver;
}
form .data input:focus{
  border-color: #3498db;
  border-bottom-width: 2px;
}
form .forgot-pass{
  margin-top: -8px;
}
form .forgot-pass a{
  color: #3498db;
  text-decoration: none;
}
form .forgot-pass a:hover{
  text-decoration: underline;
}
form .btn{
  margin: 30px 0;
  height: 45px;
  width: 100%;
  position: relative;
  overflow: hidden;
}
form .btn .inner{
  height: 100%;
  width: 300%;
  left: -100%;
  position: absolute;
  background: -webkit-linear-gradient(right, #00aa25, #5afd0e, #00aa25, #5afd0e);
  transition: all 0.4s;
}
form .btn:hover .inner{
  left: 0;
}
form .signup-link{
  text-align: center;
}
form .signup-link a{
  color: #3498db;
  text-decoration: none;
}
form .signup-link a:hover{
  text-decoration: underline;
}
.submit-button{
  height: 100%;
  width: 100%;
  background: none;
  border: none;
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;
  position: absolute;
  z-index: 3;
  left: 0;
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position:fixed;
  background: white;
  width: 80%;
  height: auto;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
}

.modalx {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-mainx {
  position:fixed;
  background: white;
  width: 100%;
  height: 100%;
  top:45%;
  left:50%;
  transform: translate(-50%,-50%);
}
.display-block {
  display: block;
}

form .data label{
  font-size: 18px;
}
form .data input, select, textarea{
  height: 100%;
  width: 100%;
  padding-left: 10px;
  font-size: 17px;
  border: 1px solid silver;
}
form .data input:focus{
  border-color: #3498db;
  border-bottom-width: 2px;
}
form .data textarea:focus{
  border-color: #3498db;
  border-bottom-width: 2px;
}
form .data section:focus{
  border-color: #3498db;
  border-bottom-width: 2px;
}
.centers {
  margin:2% 45%;
}
